  .features__list {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 7px;
    padding-left: 5px;
    padding-right: 5px; }
  .features__item {
    display: block;
    box-sizing: border-box;
    width: 230px;
    min-height: 160px;
    margin: 0;
    padding-top: 95px;
    font-size: 12px;
    line-height: 16px;
    border: none;
    box-shadow: 0px 10px 18px 2px rgba(0, 0, 0, 0.05);
    background-position: center top 21px;
    opacity: 0.7; }
    .feature-quality:checked ~ .features__list .features__item--quality {
      background-position: center top 43px;
      background-color: #ffffff;
      width: 280px;
      min-height: 200px;
      padding-top: 118px;
      z-index: 10;
      opacity: 1;
      font-size: 14px;
      line-height: 20px;
      position: relative;
      margin-left: -30px;
      left: 15px; }
    .feature-deadline:checked ~ .features__list .features__item--deadline {
      background-position: center top 49px;
      background-color: #ffffff;
      width: 280px;
      min-height: 200px;
      padding-top: 118px;
      z-index: 10;
      opacity: 1;
      font-size: 14px;
      line-height: 20px;
      position: relative;
      margin-left: -30px;
      left: 15px; }
    .feature-base:checked ~ .features__list .features__item--base {
      background-position: center top 43px;
      background-color: #ffffff;
      width: 280px;
      min-height: 200px;
      padding-top: 118px;
      z-index: 10;
      opacity: 1;
      font-size: 14px;
      line-height: 20px;
      position: relative;
      margin-left: -30px;
      left: 15px; }
  .features__item-text--dase {
    width: 185px; }
  .features-control {
    width: 36px; }
  .features-control__toggle {
    width: 8px;
    height: 8px; }
    .features-control__toggle::after {
      top: 0;
      left: -3px; } }

/*-- Desktop --*/
@media (min-width: 1200px) {
  .features__list {
    flex-direction: row;
    padding: 0;
    width: 1185px;
    margin-left: -5px; }
  .features__item {
    color: #ffffff;
    opacity: 0.6;
    background-color: inherit;
    background-position: center top 23px;
    z-index: 10;
    font-size: 15px;
    line-height: 20px;
    width: auto;
    padding-top: 100px;
    margin-left: 0px;
    min-height: 200px;
    position: static; }
    .feature-quality:checked ~ .features__list .features__item--quality {
      color: #ffffff;
      opacity: 0.6;
      background-color: inherit;
      background-position: center top 23px;
      z-index: 10;
      font-size: 15px;
      line-height: 20px;
      width: auto;
      padding-top: 100px;
      margin-left: 0px;
      min-height: 200px;
      position: static; }
    .feature-deadline:checked ~ .features__list .features__item--deadline {
      color: #ffffff;
      opacity: 0.6;
      background-color: inherit;
      background-position: center top 23px;
      z-index: 10;
      font-size: 15px;
      line-height: 20px;
      width: auto;
      padding-top: 100px;
      margin-left: 0px;
      min-height: 200px;
      position: static; }
    .feature-base:checked ~ .features__list .features__item--base {
      color: #ffffff;
      opacity: 0.6;
      background-color: inherit;
      background-position: center top 23px;
      z-index: 10;
      font-size: 15px;
      line-height: 20px;
      width: auto;
      padding-top: 100px;
      margin-left: 0px;
      min-height: 200px;
      position: static; }
  .features__item--quality {
    background-position: center top 12px;
    background-image: url("../img/like-desktop.png"); }
  .features__item--deadline {
    background-position: center top;
    background-image: url("../img/handshake-desktop.png"); }
  .features__item--base {
    background-position: center top 7px;
    background-image: url("../img/settings-desktop.png"); }
  .features__item-text--quality {
    width: 200px; }
  .features__item-text--deadline {
    width: 170px; }
  .features__item-text--dase {
    width: 230px; }
  .features-control {
    display: none; } }

.services {
  margin-bottom: 500px; }

.services__title {
  text-transform: uppercase;
  font-size: 20px;
  color: #000000;
  display: inline-block;
  margin--top: 0;
  margin-bottom: 25px;
  padding-left: 20px; }

.services__list {
  margin: 0;
  padding: 0; }

.services__item {
  cursor: pointer;
  position: relative;
  box-sizing: border-box;
  margin-bottom: 10px; }

.services__chapter {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  min-height: 50px;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  padding-left: 20px;
  padding-right: 20px;
  background-color: #f4f4f4; }
  .services__chapter::after {
    position: absolute;
    content: "";
    right: 21px;
    top: 22px;
    border: 4px solid transparent;
    border-top: 5px solid transparent;
    border-top-color: #aeadad; }

.services__item--active {
  margin-bottom: 0; }
  .services__item--active .services__chapter {
    font-size: 16px;
    background-color: #e7e7e7;
    border: 1px solid #00ffd8;
    box-shadow: 0px 2px 6.3px 0.7px rgba(0, 255, 216, 0.65); }
    .services__item--active .services__chapter::after {
      border-top-color: #000000;
      top: 24px; }
  .services__item--active .services__sublist {
    display: block;
    margin-top: 5px; }

.services__sublist {
  display: none;
  padding: 0; }

.services__subitem {
  display: flex;
  align-items: center;
  background-color: #ffffff;
  border-bottom: 1px solid #e8e8e8;
  font-size: 12px;
  line-height: 16px;
  padding-left: 20px;
  padding-right: 25px;
  margin-bottom: 0;
  min-height: 40px; }

.services__phrase-invisible {
  display: none; }

